import React, { Component } from 'react';
import OutputCard from '../components/OutputCard';
import { isMobile } from 'react-device-detect';
import CopyToClipboard from 'react-copy-to-clipboard';
import { ic_chevron_left } from 'react-icons-kit/md/ic_chevron_left';
import { ic_chevron_right } from 'react-icons-kit/md/ic_chevron_right';
import { Icon } from 'react-icons-kit';

class Page4 extends Component {
  constructor(props) {
    super(props);
    this.state = { current: 1 };
  }

  componentDidMount() {}

  previous(current) {
    if (current > 1) {
      this.setState({ current: current - 1 });
    }
    console.log('previous', current);
  }

  next(current) {
    if (current < this.props.output.length) {
      this.setState({ current: current + 1 });
    }
    console.log('next', current);
  }

  render() {
    const { profile, output, nextPage, goBack } = this.props;
    const { current } = this.state;
    console.log(output);
    let prob, emotion;
    if (
      output[current - 1].emotion &&
      output[current - 1].emotion.emotion &&
      output[current - 1].emotion.emotion.probabilities
    ) {
      prob = output[current - 1].emotion.emotion.probabilities;
    }
    if (
      output[current - 1].emotion &&
      output[current - 1].emotion.emotion &&
      output[current - 1].emotion.emotion.emotion
    ) {
      emotion = output[current - 1].emotion.emotion.emotion;
    }
    return (
      <div className="page4">
        <div className="header-container">
          <div className="back-container">{isMobile && <h4 onClick={() => goBack()}>Back</h4>}</div>
          <div className="button-container">
            <button className="button" text="Retake" onClick={() => nextPage()}>
              Retake
            </button>
          </div>
        </div>
        <div className="page4-padded">
          <div className={emotion === 'Happy' ? 'active' : 'normal'}>
            Happy <br /> {prob ? (prob.Happy * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Angry' ? 'active' : 'normal'}>
            Angry <br /> {prob ? (prob.Angry * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Excited' ? 'active' : 'normal'}>
            Excited <br /> {prob ? (prob.Excited * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Sarcasm' ? 'active' : 'normal'}>
            Sarcasm <br /> {prob ? (prob.Sarcasm * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Sad' ? 'active' : 'normal'}>
            Sad <br /> {prob ? (prob.Sad * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Fear' ? 'active' : 'normal'}>
            Fear <br /> {prob ? (prob.Fear * 100).toFixed(2) : ''}%
          </div>
          <div className={emotion === 'Bored' ? 'active' : 'normal'}>
            Bored <br /> {prob ? (prob.Bored * 100).toFixed(2) : ''}%
          </div>
        </div>
        {!isMobile && (
          <div className="output-container">
            <div className="output-card-container" style={{ padding: '0 6em' }}>
              <h5>Result</h5>
              <OutputCard>
                <br />
                {output[current - 1].nepali}
              </OutputCard>
              <CopyToClipboard text={output[current - 1].nepali}>
                <div
                  style={{ color: '#00a2ff', cursor: 'pointer', textAlign: 'center' }}
                  onClick={() => console.log('a')}
                >
                  <h4>Copy to Clipboard</h4>
                </div>
              </CopyToClipboard>
            </div>
            <div className="output-card-container" style={{ padding: '0 6em' }}>
              <h5>Output</h5>
              <OutputCard>
                <br />
                {output[current - 1].english}
              </OutputCard>
              <CopyToClipboard text={output[current - 1].english}>
                <div
                  style={{ color: '#00a2ff', cursor: 'pointer', textAlign: 'center' }}
                  onClick={() => console.log('a')}
                >
                  <h4>Copy to Clipboard</h4>
                </div>
              </CopyToClipboard>
            </div>
          </div>
        )}
        {isMobile && (
          <div className="output-mobile-container">
            <div className="output-card1-container">
              <p>Result</p>
              <OutputCard>
                <br />
                {output[current - 1].nepali}
              </OutputCard>
            </div>
            <div className="output-card2-container">
              <p>Output</p>
              <OutputCard>
                <br />
                {output[current - 1].english}
              </OutputCard>
            </div>
          </div>
        )}
        {output.length > 1 && (
          <div className="pagination">
            <span onClick={() => this.previous(current)}>
              <Icon icon={ic_chevron_left} size={32} style={{ opacity: current === 1 ? 0.4 : 1, cursor: 'pointer' }} />
            </span>
            Page {current} of {output.length}
            <span onClick={() => this.next(current)}>
              <Icon
                icon={ic_chevron_right}
                size={32}
                style={{ opacity: current === output.length ? 0.4 : 1, cursor: 'pointer' }}
              />
            </span>
          </div>
        )}
      </div>
    );
  }
}
export default Page4;



